.paragraph {
  padding-bottom: 5px;
}

.button {
  margin-left: 48px;
  height: 25px;
  font-size: 14px;
  background-color: var(--delete-button-color);
  border: 1px solid var(--input-boarder-color);
  border-radius: 4px;
  box-shadow: rgba(27, 31, 35, 0.24) 0 1px 0,
    rgba(255, 255, 255, 0.25) 0 1px 0 inset;
  box-sizing: border-box;
  color: #24292e;
  cursor: pointer;
  transition: opacity 250ms ease-in-out 150ms;
}

.button:hover {
  background-color: var(--delete-button-hover);
  text-decoration: none;
  transition-duration: 0.25s;
}

.button:active {
  background-color: var(--delete-button-active);
  box-shadow: rgba(225, 228, 232, 0.2) 0 1px 0 inset;
  transition: none 0s;
}

.button:focus {
  background-color: var(--delete-button-hover);
  outline: 1px transparent;
  transition-duration: 0.25s;
  opacity: 1;
}
